header {
  width: 90vw;
  height: 10vh;
  position: fixed;
  top: 0;
}

header .nav-top {
  width: 100vw;
  font-size: .7rem;
  position: fixed;
}

header .nav-top ul {
  justify-content: center;
  align-items: center;
  gap: 2vw;
  display: flex;
}

header .nav-top ul li {
  list-style: none;
}

header .nav-top ul li a {
  color: #faebd7;
  text-decoration: none;
}

header .nav-top ul li .nav-li-1 {
  animation: 6s 2 li-1;
  position: relative;
}

header .nav-top ul li .nav-li-2 {
  animation: 4s 2 li-1;
  position: relative;
}

header .nav-top ul li .nav-li-3 {
  animation: 4s 2 li-2;
  position: relative;
}

header .nav-top ul li .nav-li-4 {
  animation: 6s 2 li-2;
  position: relative;
}

@keyframes li-1 {
  0% {
    opacity: 0;
    color: #fff;
    background-color: #65f4da;
    left: -100px;
  }

  50% {
    opacity: 1;
    left: 0;
  }

  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes li-2 {
  0% {
    opacity: 0;
    color: #fff;
    background-color: #65f4da;
    left: 100px;
  }

  50% {
    opacity: 1;
    left: 0;
  }

  100% {
    opacity: 1;
    left: 0;
  }
}

.nav-li-1:hover, .nav-li-2:hover, .nav-li-4:hover, .nav-li-3:hover {
  color: #65f4da;
}

#menuToggle {
  width: 30vw;
  z-index: 110;
  padding-top: 1vh;
  display: block;
  position: relative;
  top: 5vh;
  left: 3vw;
}

#menuToggle a {
  color: #65f4da;
  text-decoration: none;
  transition: color .3s;
}

#menuToggle a:hover {
  color: #65f4da94;
}

#menuToggle input {
  width: 40px;
  height: 32px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  display: block;
  position: absolute;
  top: -7px;
  left: -5px;
}

#menuToggle span {
  width: 33px;
  height: 4px;
  z-index: 1;
  transform-origin: 4px 0;
  background-color: #65f4da;
  border-radius: 3px;
  margin-bottom: 5px;
  transition: transform .5s cubic-bezier(.77, .2, .05, 1), background .5s cubic-bezier(.77, .2, .05, 1), opacity .55s;
  display: block;
  position: relative;
}

#menuToggle span:nth-of-type(2) {
  width: 23px;
}

#menuToggle span:nth-of-type(3) {
  width: 13px;
}

#menuToggle span:first-child {
  transform-origin: 0 0;
  color: #000;
}

#menuToggle span:nth-last-child(2) {
  transform-origin: 0 100%;
}

#menuToggle input:checked ~ span {
  opacity: 1;
  background: #65f4da94;
  transform: rotate(45deg)translate(-2px, -1px);
}

#menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0)scale(.2);
}

#menuToggle input:checked ~ span:nth-last-child(2) {
  width: 33px;
  background: #65f4da94;
  transform: rotate(-45deg)translate(0, -1px);
}

#menu {
  width: 70vw;
  height: 70vh;
  z-index: 2;
  background: #0a192f91;
  margin: 4vh 0 0 -90vw;
  padding: 50px 60px 60px;
  list-style-type: none;
  transition: transform 1s ease-in;
  position: relative;
  left: 100px;
  transform: translateX(-110px);
}

#menu li {
  padding: .9rem .3rem;
  font-size: 1.3rem;
  font-weight: 200;
}

#menuToggle input:checked ~ ul {
  width: 70vw;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  transform: translateX(60vw);
}

